<template>
  <!--  -->
  <div class="management">
    <my-el-header/>
    <div class="container">
      <div class="nav-form">
        <el-form ref="form" class="form" :model="form" label-width="80px" :inline="true">
          <el-form-item label="">
            <el-input placeholder="" v-model="form.deptName"></el-input>
          </el-form-item>
          <el-button type="primary" @click="find()"></el-button>
          <el-button @click="clear()"></el-button>

        </el-form>
        <div class="add">
          <el-button type="primary" @click="showForm('')"></el-button>
        </div>
      </div>
      <el-dialog :title="title" :visible.sync="dialogFormVisible" destroy-on-close>
        <el-form ref="addForm" :model="addForm" :rules="rules">
          <el-form-item label="" :label-width="formLabelWidth" prop="deptName">
            <el-input class="input" v-model="addForm.deptName" autocomplete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="orderNum">
            <el-input type="number" class="input" v-model="addForm.orderNum" autocomplete="off"
                      placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="parentId">

            <!--          <my-tree style="width: 100%" :options="formList" @getValue="getSelectedValue"></my-tree>-->
            <!--          <my-el-tree v-model="addForm.parentId" :options="formList" :value='row.parentId' :props="defaultProps"/>-->
            <treeselect v-model="addForm.parentId" :multiple="false" :options="formList" :normalizer="normalizer" />
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-radio-group v-model="addForm.status">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm('addForm')"> </el-button>
        </div>
      </el-dialog>
      <div>
      </div>
      <el-tree
        @node-click="handleNodeClick"
        :data="formList"
        ref="tree"
        :default-expanded-keys="defaulExpanded"
        :expand-on-click-node="false"
        node-key="deptId"
        :filter-node-method="filterNode"
        :props="defaultProps"
        accordion>
        <div class="custom-tree-node" slot-scope="{node, data}">
          <div class="tree">
            <a class="detpName">  {{ data.deptName }} </a>
            <div>{{data.createTime}}</div>
            <div v-if="data.status === 0"></div>
            <div v-else></div>
            <div class="item-r">
              <el-link type="primary" class="link" @click="currentShow(0)"></el-link>
              <el-link type="primary" class="link" @click="currentShow(1)"></el-link>
              <el-link type="primary" class="link" @click="currentShow(2)"></el-link>
            </div>
          </div>
        </div>
      </el-tree>
    </div>
  </div>
</template>

<script>
import {
  addOrganize,
  delOrganizeList,
  getOrganizeList,
  upDataOrganize
} from "@/newwork/system-colltroner";
import {fn, getNowFormatDate,} from "@/uti";
import { isValidNumber } from "@/util/validate";
import myTree from "@/components/myTree";
import myFooter from "@/components/myFooter";
import myElTree from "@/components/my-el-tree";
import treeselect from '@riophae/vue-treeselect'
// import the styles
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import myElHeader from "@/components/myElHeader";

export default {
  name: "serve-manage",
  data: function() {
    const validateNum = (rule, value, callback) => {
      if (!isValidNumber(value)) {
        callback(new Error("ip,"));
      } else {
        callback();
      }
    };
    return {
      normalizer(node){
        if (node.children && !node.children.length) {
          delete node.children
        }
        return {
          id: node.deptId,
          children: node.children,
          label: node.deptName
        }
      },
      title: "",
      dialogFormVisible: false,
      dialogFormVisibles: false,
      formLabelWidth: "120px",
      form: {},
      defaulExpanded: [],
      addForm: {
        deptName: "",  //
        orderNum: "", //
        parentId: "", //
        status: 0 //
      },
      clickCount: 0,

      parentName: "", //
      row: {},  // 
      formatList: [],
      stats: [
        { label: "", value: "" },
        { label: "", value: "" }
      ],
      defaultProps: {
        children: "children",
        label: "deptName"
      },
      currentNodeKey: '',
      showTitle: ['','', ''],
      showIndex: null,
      list: [], // 
      rules: {
        deptName: [
          { required: true, message: " ", trigger: "blur" }
        ],
        orderNum: [
          { required: true, message: " ", trigger: "blur" },
          { validator: validateNum, message: "", trigger: "blur" }
        ],
        ancestors: [
          { required: true, message: " ", trigger: "blur" }
        ],
        parentId: [
          { required: true, message: " ", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    getOrganizeList(form) {
      getOrganizeList(form).then(res => {
        console.log(res);
        this.$emit('total', res.data.data.total)
        this.list = res.data.data
      }).catch(e => {
        this.$message.error(e)
      });
    },
    filterNode(value, data) {
      console.log(value, data);
      if (!value) return true;
      return data.deptName.indexOf(value) !== -1;
    },
    find(){
      this.$refs.tree.filter(this.form.deptName);
    },
    clear(){
      if(Object.keys(this.form).length !==0){
        this.form = this.$options.data().form
        this.getOrganizeList(this.form)
      }
    },
    handleNodeClick(data) {
      this.row = data;
      this.showIndex === null ? console.log('1') : this.showForm(this.showTitle[this.showIndex]);
    },
    showForm(type) {
      if (type === "") {
        this.dialogFormVisible = true;
        this.title = type;
        this.addForm.deptId = this.row.deptId;
        this.addForm.deptName = this.row.deptName;
        this.addForm.orderNum = this.row.orderNum;
        this.addForm.parentId = this.row.parentId
      } else if(type === '') {
        this.dialogFormVisible = true;
        this.title = type;
        this.addForm = this.$options.data().addForm;
        this.addForm.parentId = this.row.deptId;
        // this.$nextTick(() => {
        //   this.$refs.myTree.valueName = this.row.deptName
        // })
      }else {
        this.removeIt()
        this.showIndex = 0
        console.log('--------');
      }
    },
    currentShow(index) {
      this.showIndex = index;
    },
    removeIt() {
      delOrganizeList(this.row.deptId).then(res => {
        if (res.data.code === 200) {

          this.defaulExpanded.push(this.row.parentId)
          this.getOrganizeList(this.form);
          this.$message.success("");
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    getSelectedValue(value){
      this.addForm.parentId = value
    },
    submitForm(addForm) {
      this.$refs[addForm].validate((valid) => {
        if (valid) {
         this.title === '' ? addOrganize(this.addForm).then(res => {
            if(res.data.code === 200){
              this.dialogFormVisible = false
              this.getOrganizeList(this.form)
              this.$message.success('')
              // this.getOrganizeList(this.form)
            }else {
              this.$message.error(res.data.msg)
            }
          }).catch(e => {
            this.$message.error(e)
          }) : upDataOrganize(this.addForm).then(res => {
           if(res.data.code === 200){
             this.dialogFormVisible = false
             this.getOrganizeList(this.form)
             this.defaulExpanded.push(this.row.deptId)
             this.$message.success('')
           }else {
             this.$message.error(res.data.msg)
           }
         })
        }
      });
    }
  },
  components: {
    myTree,
    myFooter,
    myElTree,
    treeselect,
    myElHeader
  },
  created() {
    this.getOrganizeList(this.form);
  },
  computed: {
    createTime() {
      return this.addForm.createTime = getNowFormatDate();
    },
    formList(){
      return fn(this.list)
    },
    parentId(){
      return parseInt(this.row.parentId)
    }
  },
};
</script>

<style>
.management{
  display: flex;
  height: calc(100vh - 160px);
  justify-content: space-between;
  flex-direction: column;
}

.input {
  width: 30%;
}

.operate a {
  margin: 0 10px;
}

.custom-tree-node {
  width: 100%;
}

.tree {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tree .link {
  margin-right: 20px;
}

.tree .detpName {
  width: 200px;
}
.nav-form{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  margin: 10px 0;
  min-width: 900px;
}
.form {
  height: 40px;
}
.add{
  text-align: right;
}
</style>
